<template>
  <div class="main__wrappist">
    <h2 class="main__subtitle">Животные</h2>
    <div class="main__list">
      <li class="main__item" v-for="item in DataAnimals" :key="item">
        <router-link
          class="main__link"
          :to="{
            name: 'SpeciesPage',
            params: {
              itemRout: `${item.routing}`,
              itemName: `${item.name}`,
              itemDescription: `${item.description}`,
              itemImg: `${item.img}`,
              itemNumber: `${item.number}`,
              itemCoord: `${JSON.stringify(item.coordinates)}`,
              itemPrevious: `${item.previous}`,
              itemRef: item.ref,
              itemInfo: item.status,
            },
          }"
          >{{ item.name }}</router-link
        >
      </li>
    </div>
    <input-species :data="DataAnimals"></input-species>
    <router-link @click="removeLocalStorage()" class="back" to="/"
      >Обратно</router-link
    >
  </div>
</template>

<script>
import { DataAnimals } from "./animals";
import inputSpecies from "../input-species.vue";
export default {
  name: "AnimalsData",
  components: {
    inputSpecies,
  },
  data() {
    return {
      DataAnimals,
    };
  },
  methods: {
    setToLocalStorage() {
      localStorage.setItem("data", JSON.stringify(DataAnimals));
      localStorage.setItem("previous", "/animals");
    },
    removeLocalStorage() {
      localStorage.removeItem("data");
      localStorage.removeItem("previous");
    },
  },
  mounted() {
    this.setToLocalStorage();
  },
};
</script>

